<template>
<transition name="slide-fade">
	<div>
		<div class="cc-top">
			<div class="cc-name">{{job.name}}</div>
			<div class="cc-price">{{job.payStr}}</div>
			<div class="cc-time flexstart">
				<span>更新： {{job.updateTime}}</span>
				<span>浏览： {{job.readNum}}人</span>
			</div>
		</div>
		<div class="cc-top">
			<div class="cc-lr flexstart">
				<div class="cc-l">职位：</div>
				<div class="cc-r">{{job.cateName}}</div>
			</div>
			<div class="cc-lr flexstart">
				<div class="cc-l">要求：</div>
				<div class="cc-r">招{{job.num}}人 / {{job.educationStr}} / {{job.experienceStr}}</div>
			</div>
			<div class="cc-lr flexstart">
				<div class="cc-l">福利：</div>
				<div class="cc-r flexstart">
					<span  v-for="(item1,index1) in job.welfares" :key="index1">{{item1}}</span>
				</div>
			</div>
			<div class="cc-lr flexstart">
				<div class="cc-l">地址：</div>
				<div class="cc-r flexstart">{{job.city}} {{job.address}}</div>
			</div>
		</div>
		<div class="cc-company spacebetween">
			<div class="cc-l">
				<div class="cc-company1">{{company.name}}</div>
<!-- 				<div class="cc-name">联系人： {{company.linkman}}</div> -->
			</div>
			<div class="cc-r" v-if="tgbrole1==1&&tgbissign1==1"><a :href="'tel:'+company.hrMobile"><img class="cc-logo" src="../assets/img/phone4.png" alt=""></a></div>
			<div class="cc-r" v-if="tgbrole1==1&&tgbissign1!=1" @click="tel"><img class="cc-logo" src="../assets/img/phone4.png" alt=""></div>
		</div>
		<div class="cc-top">
			<div class="cc-lr flexstart">
				<div class="cc-l">规模：</div>
				<div class="cc-r">{{company.sizeStr}}</div>
			</div>
			<div class="cc-lr flexstart">
				<div class="cc-l">行业：</div>
				<div class="cc-r">{{company.industry}}</div>
			</div>
			<div class="cc-lr flexstart">
				<div class="cc-l">地址：</div>
				<div class="cc-r">{{company.city}}{{company.address}}</div>
			</div>
		</div>
		<div class="cc-title">岗位要求</div>
		<div class="cc-des" v-html="job.explain"></div>
		<div class="cc-title">企业介绍</div>
		<div class="cc-des" v-html="company.content"></div>
		<div style="height: 50px;"></div>
		<div class="cc-button" v-show="tgbrole1==1&&!job.delivery" @click="jobDelivery">申请职位</div>
		<div class="cc-button active" v-show="job.delivery" >已申请</div>
	</div>
</transition>
</template>
<script type="text/javascript">
import {Toast,Dialog} from 'vant';
export default {
	name: 'Home',
	components: {
		[Dialog.name]: Dialog,
		[Toast.name]: Toast,
	},
	data() {
		return {
			id:"",
			job:"",
			company:"",
			tgbrole1:"",
			tgbissign1:""
		};
	},
	mounted() {
		this.id=this.$route.query.id;
		this.jobDetail();
		this.tgbrole1=localStorage.getItem("tgbrole1");
		this.tgbissign1=localStorage.getItem("tgbissign1");
	},
	methods: {
		goto(){
			this.$router.replace({
				path:"/"
			})
		},
		tel(){
			var that=this;
			Dialog.confirm({
				title: '提示',
				message: '您尚未登记个人信息，无法申请岗位，请完善个人信息。'
			}).then(() => {
				that.$router.push({
					path:"/home"
				})
			}).catch(() => {
			});
		},
		jobDetail() {
			var that=this;
			this.$http.get(`/main/jobDetail?jobId=${that.id}`).then(function (res) {
				if (res.code==10000) {
					that.job=res.data.job;
					that.company=res.data.company;
				}else{
					Toast.fail(res.msg);
				}
			}).catch(function () {
			});
		},
		jobDelivery() {
			var that=this;
			this.$http.get(`/main/jobDelivery?jobId=${that.id}`).then(function (res) {
				if (res.code==10000) {
					that.job.delivery=true
					Dialog.alert({
						title: '提示',
						message: '您已报名成功，我们会尽快与您联系，请保持手机畅通！',
						confirmButtonText:'我知道了'
					}).then(() => {
					});
				}else if(res.code==30002){
					let isrole = localStorage.getItem("tgbrole1");
					if (isrole==1) {
						Dialog.confirm({
							title: '提示',
							message: '您尚未登记个人信息，无法申请岗位，请完善个人信息。'
						}).then(() => {
							that.$router.push({
								path:"/home"
							})
						}).catch(() => {
						});
					}else{
						Toast.success(res.msg);
					}
				}else{
					Toast.fail(res.msg);
				}
			}).catch(function () {
			});
		},
	}
}
</script>
<style lang="scss" scoped>
.cc-top{
	padding:10px 15px;
	background:#fff;
	line-height: 24px;
	margin-bottom: 1px;
	.cc-name{
		font-size:18px;
		font-weight: bold;
		color:#333;
		padding:5px 0 14px;
	}
	.cc-price{
		font-size:20px;
		color:#FF8056;
		margin-bottom: 10px;
	}
	.cc-time{
		span{
			width: 172px;
			font-size:10px;
			color:#888;
		}

	}
	.cc-lr{
		padding-bottom: 5px;
		color:#666;
		.cc-l{
			width: 45px;
		}
		.cc-r{
			width: 250px;
			flex-wrap: wrap;
			span{
				height: 16px;
				padding:1px 3px;
				border: 1px solid #FF8056;
				color:#FF8056;
				margin:3px 4px 0 0;
				font-size:10px;
				line-height: 14px;
				border-radius:2px;
			}
		}
	}
}
.cc-company{
	padding:15px;
	background:#fff;
	line-height: 36px;
	margin: 10px 0 2px;
	.cc-l{
		width: 250px;
		.cc-company1{
			font-size:17px;
			color:#333;
		}
		.cc-name{
			color:#666;
		}
	}
	.cc-r{
		width: 95px;
		img{
			margin:0 auto;
			width: 36px;
			height: 36px;
		}
	}
}
.cc-title{
	background:#fff;
	line-height: 45px;
	padding-left: 15px;
	font-size: 16px;
	font-weight: 600;
	color:#333;
	margin: 10px 0 1px;
}
.cc-des{
	padding:15px;
	background:#fff;
	color:#888;
}
.cc-button{
	position: fixed;
	left: 0;
	bottom: 0;
	width: 375px;
	height: 50px;
	line-height: 50px;
	text-align: center;
	font-size:16px;
	color:#fff;
	background: #FF8056;
	&.active{
		background:#ccc;
	}
}
</style>